import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import ListItemText from "@mui/material/ListItemText";
import {clearBasket} from "../redux/ProductsReducer";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide(props) {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
        props.onSend();

    };

    const handleClose = () => {
        setOpen(false);
        props.ClearBasket()
    };

    return (
        <div>
            <button onClick={handleClickOpen} disabled={props.checkForm} type="button" className="btn btn-success">Оплатить</button>
            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogTitle>{"Ваш заказ сформирован"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-slide-description">
                        Ваш заказ будет доставлен к назначенной дате, будьте дома. Приятного аппетита!
                    </DialogContentText>
                    {props.basket.map(b => (
                        <ListItemText primary={b.title} secondary={b.count + " шт."} />
                    ))}
                    Общая сумма заказа - {props.totalPrice + " руб"}
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Понял, спасибо</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}