import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import Basket from "./basket";
import {ClearBasket, DeleteProduct, setHistoryOrder} from "../redux/ProductsReducer";
import {AuthRedirect} from "../hooks/redirect";

let mapStateToProps = (state) => {
    return {
        basket: state.Products.basket
    }
}

export default compose(connect(mapStateToProps, {DeleteProduct, ClearBasket, setHistoryOrder}), withRouter, AuthRedirect) (Basket);