const SET_PRODUCT = 'SET-PRODUCT';
const DELETE_PRODUCT = 'DELETE-PRODUCT';
const CLEAR_BASKET = 'CLEAR-BASKET';
const SET_HISTORY_ORDER = 'SET-HISTORY-ORDER';

export const setHistoryOrder = (order) => {
    return {
        type: SET_HISTORY_ORDER,
        order
    }
}


export const setProduct = (data) => {
    return {
        type: SET_PRODUCT,
        data
    }
}

export const ClearBasket = () => {
    return {
        type: CLEAR_BASKET,

    }
}

export const DeleteProduct = (title) => {
    return {
        type: DELETE_PRODUCT,
        title
    }
}

