const ProductsReducer = (state = initialState, action) => {
    function CounterProduct() {
       let tempObj = state.basket.filter(b => b.title === action.data.title)[0]
        tempObj.count++
        tempObj.price += action.data.price
       return  [ ...state.basket]

    }

    switch (action.type) {

        case SET_HISTORY_ORDER:
            return {
                ...state,
                ...state.historyOrder.push(action.order)
            }

        case CLEAR_BASKET:
            return {
                ...state,
                basket:  []

            }

        case SET_PRODUCT:
            return {
                ...state,
                basket:  [...state.basket.filter(b => b.title === action.data.title)].length === 0 ? [...state.basket, action.data] :
                CounterProduct()
            }

        case DELETE_PRODUCT:
            return {
                ...state,
                basket:  [...state.basket.filter(b => b.title !== action.title)]
            }

        default: return state;
    }
}

export default ProductsReducer;