import React from "react";
import style from "./profile.module.css"

const OrdersHistory = ({historyOrder}) => {
    return <div>
        <br/>
        <div className="card">

            <h3>История заказов</h3>

            <ul className="list-group list-group-flush">
                <div className={style.wrapOrders}>
                {historyOrder.map(h => (
                    <li className="list-group-item"><b>{"Заказ оформлен на: "}</b>  {h.lastName + " " + h.firstName + " " + h.patronymic}<br/>
                        <b>{"Общая сумма заказа: "}</b>  {h.price + " руб"}<br/>
                            <b>{"Способ оплаты: "}</b>  {h.payment}<br/>
                                <b>{"Дата доставки: "}</b>  {h.date}<br/>
                    </li>
                ))}
                </div>
            </ul>

        </div>
        </div>

}

export default OrdersHistory;