import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import {Container, CssBaseline, IconButton, Stack, Tooltip} from "@mui/material";
import style from './basket.module.css'
import Grid from "@mui/material/Grid";
import DeleteIcon from '@mui/icons-material/Delete';
import {FormForPay} from "./FormForPay";

const Basket = ({basket, DeleteProduct, ClearBasket, setHistoryOrder}) => {

    let totalPrice = 0;

    for (let i=0; i< basket.length; i++) {
        totalPrice += basket[i].price;
    }

    return (
        <React.Fragment>
            <CssBaseline />
            <Container Width="sm">
                {basket.length === 0 ?

                <h1>
                    <br/>
                    Пополните корзину, Вы явно хотите кушать!</h1> :
                <div>

                        <h1>
                        <br/>
                        Ваши товары:</h1>

                    <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                        {basket.map(bas =>
                        <ListItem>
                            <ListItemAvatar>
                                <img className={style.imgProduct} src={bas.img} />
                            </ListItemAvatar>
                            <ListItemText primary={bas.title} secondary={bas.price + " руб, " + bas.count + " шт."} />

                            <Grid item xs={4}>
                                <Tooltip title="Delete">
                                    <IconButton onClick={() => {DeleteProduct(bas.title)} }>
                                        <DeleteIcon />
                                    </IconButton>
                                </Tooltip>
                            </Grid>
                        </ListItem>
                    )}
                    </List>

                    <h3><span className="badge bg-secondary">Общая сумма заказа состовляет - {totalPrice} руб</span></h3>

                    <div>
                    <FormForPay basket={basket} totalPrice={totalPrice} ClearBasket={ClearBasket} setHistoryOrder={setHistoryOrder} />
                    </div>
                </div>
                }
            </Container>
        </React.Fragment>
                )

}

export default Basket;