import React, {useEffect, useState} from "react";
import {Container, CssBaseline} from "@mui/material";
import Box from "@mui/material/Box";
import MediaCard from "./CardProduct/cardProduct";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import ReactPaginate from "react-paginate";
import style from "./products.module.css";
import {setProduct} from "../redux/ProductsReducer";

const Products = ({products, setProduct}) => {

    function FilterDepartment(department) {
        return products.filter(prod => prod.department === department);
    }

    let bakery = FilterDepartment("bakery")
    let vegetables = FilterDepartment("vegetables")
    let fastfood = FilterDepartment("fastfood")

    const ProductsBox = ({department, data, setProduct}) => {

        // PAGINATION

        let items = data;

        function Items({ currentItems }) {
            return (
                <>
                <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
                    {currentItems && currentItems.map(l =>
                        <Grid item xs={4}>
                            <MediaCard image1={l.img} key={l.id} department={l.department} title={l.title}
                                       price={l.price} description={l.description} setProduct={setProduct}/>
                        </Grid>
                    )}
                </Grid>
                </>
            );
        }

        function PaginatedItems({ itemsPerPage}) {

            const [currentItems, setCurrentItems] = useState(null);
            const [pageCount, setPageCount] = useState(0);
            const [itemOffset, setItemOffset] = useState(0);

            useEffect(() => {
                const endOffset = itemOffset + itemsPerPage;
                setCurrentItems(items.slice(itemOffset, endOffset));
                setPageCount(Math.ceil(items.length / itemsPerPage));
            }, [itemOffset, itemsPerPage]);


            const handlePageClick = (event) => {
                const newOffset = (event.selected * itemsPerPage) % items.length;
                setItemOffset(newOffset);
            };

            return (
                <>
                    <Items currentItems={currentItems} />
                    <div className="d-flex justify-content-center">
                        <div className={style.pagination}>
                            <ReactPaginate
                                nextLabel={<span className={style.paginationText}>Далее</span>}
                                onPageChange={handlePageClick}
                                pageRangeDisplayed={4}
                                marginPagesDisplayed={4}
                                pageCount={pageCount}
                                previousLabel={<span className={style.paginationText}>Назад</span>}
                                pageClassName={style.paginationText}
                                pageLinkClassName="page-link"
                                previousClassName={style.paginationText}
                                previousLinkClassName="page-link"
                                nextClassName={style.paginationText}
                                nextLinkClassName="page-link"
                                breakLabel="..."
                                breakClassName={style.paginationText}
                                breakLinkClassName="page-link"
                                containerClassName="pagination"
                                activeClassName="active"
                                renderOnZeroPageCount={null}
                            />
                        </div>


                    </div>
                </>
            );
        }

        return (
            <Box sx={{ width: '100%', pt: "50px" }}>
                <Typography sx={{fontWeight: "500"}} gutterBottom variant="h4" component="div">
                    {department}
                </Typography>

                <PaginatedItems itemsPerPage={3} />

            </Box>
        )
    }

    return (
        <React.Fragment>
            <CssBaseline />
            <Container Width="sm">
                    <ProductsBox department={"ВЫПЕЧКА"} data={bakery} setProduct={setProduct} />
                    <ProductsBox department={"ОВОЩИ"} data={vegetables} setProduct={setProduct} />
                    <ProductsBox department={"ФАСТ ФУД"} data={fastfood} setProduct={setProduct} />
            </Container>
        </React.Fragment>
    )
}

export default Products