import * as React from 'react';
import {MenuItem, Stack, TextField} from "@mui/material";
import Box from "@mui/material/Box";
import style from './basket.module.css'
import { DatePicker
} from "@material-ui/pickers";
import {Checkbox, Paper} from "@material-ui/core";
import AlertDialogSlide from "./dialogWindow";


export const FormForPay = (props) => {
    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

    const currencies = [
        { value: 'Эквайринг'},
        { value: 'Наличные'},
        { value: 'По карте'},
        { value: 'Мобильный перевод СБП'}
    ];

    const [payment, setPayment] = React.useState('');

    const onPayment = (e) => {
        setPayment(e.target.value);
    }

    const [date, setDate] = React.useState(new Date());

    const handleChangeDate = (date) => {
        setDate(date);
    }

    const [lastName, setLastName] = React.useState("");
    const onLastName = (e) => {
        setLastName(e.target.value)
    }

    const [firstName, setFirstName] = React.useState("");
    const onFirstName = (e) => {
        setFirstName(e.target.value)
    }

    const [patronymic, setPatronymic] = React.useState("");
    const onPatronymic = (e) => {
        setPatronymic(e.target.value)
    }

    const [address, setAddress] = React.useState("");
    const onAddress = (e) => {
        setAddress(e.target.value)
    }

    const [comment, setComment] = React.useState("");
    const onComment = (e) => {
        setComment(e.target.value)
    }

    const [checkForm, setCheckForm] = React.useState(true);
    const onCheck = () => {
        checkForm === false ? setCheckForm(true) : setCheckForm(false)
    }

    const onSend = () => {
        let countDate = date.getDate() < 12 ? "0" + date.getDate() : date.getDate();
        let monthDate = date.getMonth() < 12 ? "0" + date.getMonth() : date.getMonth();
        let yearDate = date.getFullYear();
        let tempDate = countDate + "." + monthDate + "." + yearDate
        props.setHistoryOrder({payment, date: tempDate, lastName, firstName, patronymic, address, comment, price: props.totalPrice})
    }

    return (
        <Box
            component="form"
            sx={{
                '& .MuiTextField-root': { m: 1, width: '25ch' },
            }}
            noValidate
            autoComplete="off"
        >
            <h2>Заполните форму для обработки заказа</h2>
                <TextField onChange={onLastName}
                    required
                    id="standard-basic"
                    variant="standard"
                    label="Фамилия"
                    placeholder="Введите фамилию"
                    value={lastName}
                />
                <TextField onChange={onFirstName}
                    required
                    id="standard-basic"
                    variant="standard"
                    label="Имя"
                    placeholder="Введите имя"
                />
                <TextField onChange={onPatronymic}
                    required
                    id="standard-basic"
                    variant="standard"
                    label="Отчество"
                    placeholder="Введите отчество"
                />
                <TextField onChange={onAddress}
                    required
                    id="standard-basic"
                    variant="standard"
                    label="Адрес"
                    placeholder="Введите адрес"
                />

                <TextField onChange={onComment}
                    id="standard-basic"
                    variant="standard"
                    label="Комментарий"
                    placeholder="Введите комментарий к заказу"
                />

                <TextField
                    id="standard-basic"
                    variant="standard"
                    select
                    label="Способ оплаты"
                    value={payment}
                    onChange={onPayment}
                    helperText="Выберите вариант оплаты"
                >
                    {currencies.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.value}
                        </MenuItem>
                    ))}
                </TextField>



                    <DatePicker
                        onChange={handleChangeDate}
                        value={date}
                        label="Выберите дату"
                    />
                <div className={style.payDiv}>
                    <br/>
                    <br/>
                    <Stack direction="row" spacing={2}>
                    <Checkbox onClick={onCheck} {...label} />
                    <h4>Согласен на обработку персональных данных</h4><br/>
                        <AlertDialogSlide basket={props.basket} totalPrice={props.totalPrice} checkForm={checkForm} onSend={onSend} ClearBasket={props.ClearBasket} />
                    </Stack>

                </div>
        </Box>
    )
}