import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import SvgIcon from '@mui/material/SvgIcon';
import style from './header.module.css';
import {NavLink} from "react-router-dom";
import {IconButton} from "@mui/material";
import Badge from '@mui/material/Badge';
import { styled } from '@mui/material/styles';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';

const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {
        right: -3,
        top: 13,
        padding: '0 1px',
        background: '#00E31F'
    },
}));

function HomeIcon(props) {
    return (
        <SvgIcon {...props}>
            <path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z" />
        </SvgIcon>
    );
}

export default function Header(props) {
    console.log(props.basket)

    const pages = [
        {page :'Главная', link: '/products'},
        {page :'Контакты', link: '/contacts'},
        {page :'Корзина', link: '/basket', count: true}];


    return (
        <Box sx={{ flexGrow: 1 }}>

            <AppBar position="static" sx={{
                background: '#2FAF00'

            }}>
                {!!props.email &&
                <Toolbar>
                    <Typography
                        variant="h6"
                        noWrap
                        component="a"
                        href="/"
                        sx={{
                            mr: 2,
                            display: { xs: 'none', md: 'flex' },
                            fontFamily: 'monospace',
                            fontWeight: 700,
                            letterSpacing: '.3rem',
                            color: 'inherit',
                            textDecoration: 'none',
                        }}
                    >
                        ОНЛАЙН МАГАЗИН
                    </Typography>

                    <HomeIcon sx={{ fontSize: 40 }} />


                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                        {pages.map((page) => (
                            <Typography
                                key={page.page}
                                sx={{ mx: 8, color: 'white', display: 'block',
                                    fontFamily: 'arial',
                                    fontWeight: 300,
                                    fontSize: 25}}
                            >
                                <NavLink className={style.linkStyle} to={page.link}> {page.page}</NavLink>
                            </Typography>

                        ))}
                        <IconButton aria-label="cart">
                            <StyledBadge badgeContent={props.basket.length} color="secondary">
                                <ShoppingCartIcon />
                            </StyledBadge>
                        </IconButton>
                    </Box>



                    <Typography
                        variant="h6"
                        noWrap
                        component="a"
                        href="/"
                        sx={{
                            mr: 2,
                            display: { xs: 'none', md: 'flex' },
                            fontWeight: 200,
                            color: 'inherit',
                            textDecoration: 'none',
                            fontSize: 20
                        }}
                    >
                        <NavLink className={style.linkStyle} to={'/profile'}>{props.email}</NavLink></Typography>

                        <Button onClick={props.removeUser} sx={{
                        mr: 2,
                        display: { xs: 'none', md: 'flex' },
                        fontWeight: 200,
                        color: 'inherit',
                        textDecoration: 'none',
                        fontSize: 20
                    }}>Выход</Button>



                </Toolbar>
                }
            </AppBar>
        </Box>
    );
}