import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';


export default function MediaCard({image1, key, department, title, price, description, setProduct}) {

    let onBasket = (data, price) => {
       return  setProduct({
            title: title,
            price: Number(price),
            img: image1,
            count: 1
        })
    }

    return (
                <Card sx={{maxWidth: 345, height: 300}}>
                    <CardMedia
                        component="img"
                        height="140"
                        image={image1}
                        alt="green iguana"
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="div">
                            {title}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                            {description}
                        </Typography>
                    </CardContent>
                    <CardActions>
                        <Typography variant="body2" color="text.secondary">
                            {price + " руб"}
                        </Typography>
                        <Button onClick={() => {onBasket(title, price, image1)}} size="small" sx={{pl: "190px"}}>В корзину</Button>
                    </CardActions>
                </Card>
    );
}